.faq {
  & details {
    // padding-bottom: 10px;
    border-bottom: 1px solid var(--light-gray);
    // border-radius: 5px;

    &:first-of-type {
      border-top: 1px solid var(--light-gray);
    }

    & p {
      margin-left: 10px;
      margin-bottom: 20px;
      font-size: 14px;
    }
  }

  & summary {
    position: relative;
    align-items: center;
    padding: 20px 0;

    font-size: 15px;
    cursor: pointer;

    & h2 {
      width: 90%;
      font-size: 15px;
      font-weight: 400;
    }
  }

  & summary::marker {
    content: none;
  }

  & summary::after {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    content: '';
    width: 20px;
    height: 20px;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
  }

  & [open] summary::after {
    background-image: url(data:image/svg+xml;base64,);
  }
}
