a {
  text-decoration: none;
  outline: none;
  color: inherit;

  &:visited {
    color: inherit;
  }
}

:is(.navigation, .nav, .user-actions, .user-nav, .nav-section, .modal, .auth, .navbar, .developer-about, .gk-progress, .page__footer, .offer-images__main) a {
  color: inherit;

  &:focus-visible {
    // box-shadow: 0 0 0 1px rgb(255, 255, 255), 0 0 2px 3px rgb(166, 229, 255), 0 0 2px 3px rgb(0, 106, 255);
    box-shadow: inset 0 0 0 1px rgb(255, 255, 255), inset 0 0 2px 3px #74d099, inset 0 0 2px 3px #20a052;
  }
}

// green links

:is(.nav-section, .footer, .developer-about, .gk-progress) a {
  color: var(--basic-green);
}

:is(.nav-section, .footer, .new-offers__image, .new-offer, .bldg-nav, .submenu__list--expert, .subway) a {
  &:focus-visible {
    box-shadow: 0 0 0 1px rgb(255, 255, 255), 0 0 2px 3px #74d099, 0 0 2px 3px #20a052;
  }
}

//  links hover

:is(.location, .new-offers__nav, .breadcrumbs) a {
  // border-radius: 5px;

  &:hover {
    color: var(--basic-green);
  }
}

// focus

:is(.location, .new-offers__nav) a {
  border-radius: 5px;

  &:focus-visible {
    color: var(--basic-green);
    box-shadow: inset 0 0 0 1px rgb(255, 255, 255), inset 0 0 2px 3px #74d099, inset 0 0 2px 3px #20a052;
  }
}

// active

:is(.location, .new-offers__nav) a {
  &:active {
    color: red;
  }
}

:is(.new-bldg) a {
  &:hover {
    color: inherit;
  }
}

:is(.new-buildings, .journal, .gk-offers, .catalog-menu, .offer-images__main, .plan-offer, .deal-terms, .statistics__table) a {
  &:focus-visible {
    box-shadow: 0 0 0 1px rgb(255, 255, 255), 0 0 2px 3px #74d099, 0 0 2px 3px #20a052;
  }
}

:is(.breadcrumbs, .quick-links, .developer-about, .gk-progress, .catalog-list__header, .offer-properties, .seller, .offer-address) a {
  &:focus-visible {
    border-radius: 5px;
    box-shadow: 0 0 0 1px rgb(255, 255, 255), 0 0 1px 2px #74d099, 0 0 1px 2px #20a052;
  }
}
