.offer-images__wrapper {
  display: grid;
  grid-template-rows: 250px 65px;
  gap: 8px;
  border-radius: 5px;

  & img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    object-fit: cover;
  }

  &--novostroiky {
    grid-template-rows: 250px;
  }
}

.offer-images__main {
  position: relative;

  & a {
    display: block;
    height: 100%;
  }

  & .offer-images__camera,
  & .button-circle {
    position: absolute;
    top: 10px;
  }

  & .offer-images__camera {
    display: grid;
    left: 10px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: var(--light-gray);

    & svg {
      margin: auto;
    }
  }

  & .button-circle {
    right: 10px;
  }

