}

.offer-images__counter {
  position: absolute;
  bottom: 10px;
  right: 10px;
  padding: 3px 5px;
  border-radius: 3px;
  font-size: 12px;
  color: var(--white);
  background-color: rgba(0, 0, 0, 0.5);
}

.offer-images__thumbnails {
  & a {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 100%;
    grid-template-rows: 100%;
    column-gap: 8px;
  }
}

@media (min-width: @desktop-width) {
  & .offer-images__wrapper {
    display: grid;
    grid-template-rows: 282px 75px;
    gap: 8px;
    border-radius: 5px;

    &--novostroiky {
      grid-template-rows: 340px;
    }
  }

  .offer-images__main {
    & .offer-images__swiper {
      height: 100%;
    }

    & .button-circle {
      display: none;
    }
  }

  & .offer-images__camera {
    left: auto;
    right: 10px;
  }
}
